body {
    background-color: #121212;
    margin: 0;
    text-align: center;
}

.topbar {
    padding: 15px;
    display: flex;
    align-items: center;
    position: sticky;
    top: 0;
    background: #121212;
}

.topbar input {
    width: 100%;
    box-sizing: border-box;
    font-size: 19px;
    outline: none;
    border: none;
    border-radius: 25px;
    padding: 7px 43px 7px 17px;
    background-color: #333;
    color: #fff;
}

.topbar span {
    position: absolute;
    padding: 6px 13px 6px 5px;
    top: 15px;
    right: 15px;
    border-radius: 0 25px 25px 0;
    cursor: pointer;
    color: #fff;
}

.box {
    padding: 3px;
}

.column { 
    width: 20%;
    padding: 0 2px;
    display: inline-block;
    box-sizing: border-box;
    aspect-ratio: 9/9;
}

img {
    width: 100%;
    height: 100%;
    border-radius: 5px;
}

h1 {
    font-size: 17px;
    color: #fff;
    width: max-content;
    margin: 10px auto;
    text-decoration: underline;
    cursor: pointer;
}

.lds-ellipsis {
    display: inline-block;
    position: relative;
    width: 63px;
    height: 30px;
}

.lds-ellipsis div {
    position: absolute;
    top: 10px;
    width: 10px;
    height: 10px;
    border-radius: 50%;
    background: #fff;
    animation-timing-function: cubic-bezier(0, 1, 1, 0);
  }

.lds-ellipsis div:nth-child(1) {
    left: 6px;
    animation: lds-ellipsis1 0.6s infinite;
}
  
.lds-ellipsis div:nth-child(2) {
    left: 3px;
    animation: lds-ellipsis2 0.6s infinite;
}

.lds-ellipsis div:nth-child(3) {
    left: 24px;
    animation: lds-ellipsis2 0.6s infinite;
}
  
.lds-ellipsis div:nth-child(4) {
    left: 42px;
    animation: lds-ellipsis3 0.6s infinite;
}
  
@keyframes lds-ellipsis1 {
    0% {
      transform: scale(0);
    }
    100% {
      transform: scale(1);
    }
}
  
@keyframes lds-ellipsis3 {
    0% {
      transform: scale(1);
    }
    100% {
      transform: scale(0);
    }
}
  
@keyframes lds-ellipsis2 {
    0% {
      transform: translate(0, 0);
    }
    100% {
      transform: translate(24px, 0);
    }
}
  
@media screen and (max-width: 1200px) and (min-width: 901px) {
    .column {
        width: 25%;
    }
}

@media screen and (max-width: 900px) and (min-width: 601px) {
    .column {
        width: 33.3%;
    }
}

@media screen and (max-width: 600px) and (min-width: 0px) {
    .column {
        width: 50%;
    }
}